<style scoped lang="less">
  @import "./track.less";
</style>

<template>
	<section class="track">
    <el-container>
      <el-main>
        <div class="track-manager-panel">
          <div class="title" v-text="currentCar.title"></div>
          <div class="content">
            <el-row type="flex">
              <el-col :span="20">
                <el-date-picker
                  style="width:350px;"
                  v-model="pickerValue"
                  type="datetimerange"
                  :picker-options="pickerOptions"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                  value-format="timestamp"
                  align="center">
                </el-date-picker>
              </el-col>
            </el-row>
            <el-row type="flex">
              <!-- <el-col :span="12">
                <el-select v-model="trackPeroidValue" placeholder="" @change="onSelectChange">
                  <el-option
                    v-for="item in trackPeroidOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col> -->
              <el-col :span="12">
                <el-select v-model="trackSpeed" placeholder="">
                  <el-option
                    v-for="item in trackSpeedOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="12" class="track-btns">
                <div v-show="!isPlay">
                  <el-button type="primary" @click="onSearch"></el-button>
                  <el-button type="primary" @click="onPlay"></el-button>
                </div>
                <div v-show="isPlay">
                <el-button type="primary" @click="onPause" v-show="!isContinue"></el-button>
                <el-button type="primary" @click="onContinue" v-show="isContinue"></el-button>
                <el-button type="primary" @click="onStop"></el-button>
                </div>
              </el-col>
            </el-row>
            <!-- <el-row type="flex" v-if="showPicker">
              <el-col :span="20">
                <el-date-picker
                  style="width:350px;"
                  v-model="pickerValue"
                  type="datetimerange"
                  :picker-options="pickerOptions"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                  value-format="timestamp"
                  align="center">
                </el-date-picker>
              </el-col>
            </el-row> -->
            <!-- <el-row type="flex">
              <el-col :span="12">
                <el-select v-model="trackSpeed" placeholder="">
                  <el-option
                    v-for="item in trackSpeedOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="12">
                <el-switch
                  style="margin: 6px 0 0 15px"
                  v-model="switchValue"
                  active-text=""
                  inactive-text="">
                </el-switch> 
              </el-col>
            </el-row> -->
          </div>
        </div>
        <!-- sync -->
        <!-- <bdmap :mapObj.sync="mapObj"></bdmap> -->
        <!-- on emit  -->
        <map-panel :isNeedMapObj="true" @sendMapObj="getMapObj"></map-panel>
      </el-main>
      <el-aside width="320px">
        <el-tabs type="border-card">
          <el-tab-pane label="">
          <carTable @onCarTableCellClick="onCarTableCellClick"></carTable>
          </el-tab-pane>
        </el-tabs>
      </el-aside>
    </el-container>
	</section>
</template>

<script>
import {getTrack} from '@/api/drive'
import mapPanel from '@/components/map-panel.vue'
import carTable from '@/components/car-table'
import mapTrack from '@/common/js/map-track.js'
import {addCarMapOverlay} from '@/common/js/bdmap.js'
export default {
  name: 'v-track', // trackvue
  data () {
    return {
      currentPage: 1,
      trackData: [], // 
      stayPoints: [], // 
      mapObj: null, // map
      currentCar: {carNumber:'',title:''}, // 
      // trackPeroidOptions: [
        // {value: '1',label: ''},
        // {value: '2',label: '1'}, 
        // {value: '3',label: '3'}, 
        // {value: '4',label: '5'}, 
      //   {value: '1',label: '1'},
      //   {value: '2',label: '3'}, 
      //   {value: '3',label: '24'}, 
      //   {value: '4',label: '3'}, 
      //   {value: '5',label: '5'}, 
      //   {value: '6',label: ''}
      // ],
      // trackPeroidValue: '1',
      trackSpeedOptions: [
        {value: 10,label: '(10ms)'}, 
        {value: 50,label: '(50ms)'}, 
        {value: 100,label: '(100ms)'}, 
        {value: 200,label: '(200ms)'}, 
        {value: 500,label: '(500ms)'}
      ],
      travelIsPlay: true, // 
      travelIsPause: false, // 
      isPlay: false, // 
      isContinue: false, // 
      trackSpeed: 500,  // 
      // showPicker: false,
      pickerValue: '', // 
      pickerOptions: null,
      switchValue: true
    }
  },
  mounted() {
    setTimeout(() => {
      this.pickerOptions = mapTrack.getpickerOptions()
    },20)
  },
  methods:{
    _getTrack(){
      let params = {
        vehicle_number: this.currentCar.carNumber
      }
      if(!this.pickerValue){
        this.$notify.info({
          title: '',
          message: '',
          type: 'warning'
        })
        return false
      }
      params.start_time = this.pickerValue[0]
      params.end_time = this.pickerValue[1]
      // console.log(params)
      getTrack(params).then(res => {
        // 
        if(res.data.ret == 0){
          // console.log(res.data.datas)
          this.trackData = res.data.datas.points
          this.stayPoints = res.data.datas.stay_points
        }else{
          this.$message({
            message: res.data.msg,
            type: 'error'
          })
        }
      }).then(()=>{
        // 
        if(this.trackData.length){
          // this.mapObj.clearOverlays()
          mapTrack.drawMapLine(this.mapObj, this.trackData, this.stayPoints)
        }else{
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    },
    // 
    onCarTableCellClick(row, column, cell, event){
      // console.log(row)
      if(column.property == 'vehicle_number'){
        this.isPlay = false
        this.isContinue = false
        this.currentCar.carNumber = row.vehicle_number
        this.currentCar.title = row.vehicle_number
        this.mapObj.clearOverlays()
        let marker = addCarMapOverlay(row.longitude,row.latitude)
      }
    },
    // 
    onSearch(){
      // console.log(this.currentCar)
      this.mapObj.clearOverlays()
      if(this.currentCar.carNumber){
        this._getTrack()
      }else{
        this.showTip('')
      }
    },
    // 
    // onSelectChange(value){
      // this.showPicker = (value == 6) ? true : false
    // },
    // 
    onPlay() {
      if(this.currentCar.carNumber){
        this.isPlay = true
        if (this.trackData.length >= 2){
          this.travelIsPlay = true
          this.travelIsPause = false
          // this._playTravel()
          mapTrack.playTravel(this)
        }else{
          this.trackData.length = 0
          // mapClearOverlays(-1)
          this.mapObj.clearOverlays()
          // TrackQuery()
          this._getTrack()
        }
      }else{
        this.showTip('')
      }
    },
    // 
    onStop() {
      this.isPlay = false
      this.isContinue = false
      this.travelIsPlay = false
      this.travelIsPause = false
      // clearInterval(this.trackPlayTimer) // 
      // mapTrack.stopOrEndPlay(this.mapObj, 0, this.carMarker, this.carIcon, this.pointDirection)
      mapTrack.stopOrEndPlay(this, 0)
    },
    // 
    onPause() {
      this.isContinue = true
      this.travelIsPlay = false
      this.travelIsPause = true
    },
    // 
    onContinue(){
      this.isContinue = false
      this.travelIsPlay = true
      this.travelIsPause = false
    },
    // 
    getMapObj(map){
      this.mapObj = map
    },
    showTip(msg){
      this.$notify({
        title: '',
        message: msg,
        type: 'warning'
      })
    }
  },
  components:{
    mapPanel,
    carTable
  }
}
</script>



